.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 880px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  padding: 45px 0 36px;
  margin: 0 auto 39px;
}

@media screen and (max-width: 980px) {
  .header {
    max-width: 582px;
  }
}

@media screen and (max-width: 640px) {
  .header {
    max-width: 100%;
    padding-bottom: 26px;
    margin: 28px 0 42px;
  }
}
